import { Component } from '@angular/core';
import {Router} from "@angular/router";
import {Http, Headers, RequestOptions} from "@angular/http";

@Component({
  selector: 'app-login',
  template: require('./login.component.html'),
  styles: [require('./login.component.scss')]
})
export class LoginComponent {
  user = {
    "email" : "",
    "password" : ""
  };

  constructor(private http: Http,  private router: Router){

  }

  login(){


    // let headers = new Headers({ 'Accept': 'application/json' });
    // // headers.append("Content-Type", "application/x-www-form-urlencoded");
    // headers.append("Authorization", "Basic " + btoa(username._value + ":" + password._value));
    // let options = new RequestOptions({ headers: headers });
    //
    // return this.http.postForLogin("/api/login", {}, options)

    let headers = new Headers();
    headers.append('Authorization', "Basic "+ btoa( this.user.email + ":" +  this.user.password))
    // headers.append('Authorization', "Basic sdfsdfsdf");

    let options = new RequestOptions({headers: headers});
    console.log(options)
    this.http.post("http://test.joinmyevent.com/ems/ws/login/user", this.user, options).subscribe((data) => {
      let res = data.json();
      if(res.status.errorCode !== 402){
        this.router.navigate(["/dashboard/home"]);
      }
      console.log(res)
    });
  }
}



